import React from 'react';
import ColorSquare from '../colorSquare/ColorSquare';
import {
  ArrowLeftContainer,
  ArrowRightContainer,
  ArrowCenterContainer,
  ColorBoxContainer,
  NumberText,
  FateCardsContainer,
  MiddleContainer,
} from './style';
import { GladiatorIcon } from '../../assets/icons/GladiatorIcon';
import { GoodsIcon } from '../../assets/icons/GoodsIcon';
import { LeftArrow } from '../../assets/icons/LeftArrow';
import { RightArrow } from '../../assets/icons/RightArrow';
import { UpArrow } from '../../assets/icons/UpArrow';
import Divider from '@mui/material/Divider/Divider';

interface Props {
  picts_direction: string;
  gladiator: number;
  goods: number;
  resource: string[];
}

const ArrowContainer = (d) => {
  const direction = d.direction;
  switch (direction) {
    case 'left':
      return (
        <ArrowLeftContainer>
          <div style={{ height: '4em', width: '4em' }}>
            <LeftArrow />
          </div>
        </ArrowLeftContainer>
      );
    case 'right':
      return (
        <ArrowRightContainer>
          <div style={{ height: '4em', width: '4em' }}>
            <RightArrow />
          </div>
        </ArrowRightContainer>
      );
    case 'center':
      return (
        <ArrowCenterContainer>
          <div style={{ height: '4em', width: '4em' }}>
            <UpArrow />
          </div>
        </ArrowCenterContainer>
      );
    default:
      return null;
  }
};

const FateCardContainer: React.FC<Props> = ({
  picts_direction,
  gladiator,
  goods,
  resource,
}) => {
  return (
    <FateCardsContainer>
      <Divider />
      <ArrowContainer direction={picts_direction} />
      <MiddleContainer>
        <div style={{ height: '2em', width: '2em' }}>
          <GladiatorIcon />
        </div>
        <NumberText>{gladiator}</NumberText>
        <div style={{ height: '2em', width: '2em' }}>
          <GoodsIcon />
        </div>
        <NumberText>{goods}</NumberText>
      </MiddleContainer>
      <ColorBoxContainer>
        {resource.map((d) => {
          switch (d) {
            case 'black':
              return <ColorSquare color={d} />;
            case 'blue':
              return <ColorSquare color={d} />;
            case 'purple':
              return <ColorSquare color={d} />;
            case 'yellow':
              return <ColorSquare color={d} />;
            default:
              return <ColorSquare color={'grey'} />;
          }
        })}
      </ColorBoxContainer>
    </FateCardsContainer>
  );
};

export default FateCardContainer;
