import React, { useState } from 'react';
import {
  ButtonSetContainer,
  CounterContainer,
  StyledNumberText,
  StyledText,
} from './style';
// import Grid from '@mui/material/Grid/Grid';
import Button from '@mui/material/Button/Button';

interface Props {
  colorText: string;
  textColor: string;
}

const ResourceCounter: React.FC<Props> = ({ colorText, textColor }) => {
  const [count, setCounter] = useState(0);
  const minus = () => {
    if (count > 0) {
      setCounter(count - 1);
    }
  };

  const plus = () => {
    setCounter(count + 1);
  };

  return (
    <CounterContainer>
      <StyledText color={textColor}>{colorText}</StyledText>
      <ButtonSetContainer>
        <Button onClick={minus}>-</Button>
        <StyledNumberText color='black'>{count}</StyledNumberText>
        <Button onClick={plus}>+</Button>
      </ButtonSetContainer>
    </CounterContainer>
  );
};

export default ResourceCounter;
