import { createTheme, Grid, ThemeProvider } from '@mui/material';
import { useState } from 'react';
import GoogleFontLoader from 'react-google-font-loader';
import {
  AppContainer,
  AppGradientWrapper,
  AppHeaderContainer,
  AppPrimaryButton,
  AppPrimaryText,
  FateCardSection,
  ResourceCounterCard,
} from './App.styled';
import FateCardContainer from './components/fateCard';
import ResourceCounterSet from './components/resourceCounterSet';
import fateCards from './settings/fateCards';
import { FateCard } from './settings/fateCards.model';

const muiTheme = createTheme({
  typography: {
    fontFamily: "'Mitr', sans-serif",
  },
});

const App = () => {
  const [initialFateCards, setInitialFateCards] = useState<FateCard[]>(
    fateCards.sort(() => Math.random() - 0.5),
  );
  const [pickedFateCards, setPickedFateCards] = useState<FateCard[]>([]);

  const randomFateCard = () => {
    if (initialFateCards.length === 0) {
      setInitialFateCards(fateCards.sort(() => Math.random() - 0.5));
    }
    const fateCard = initialFateCards[0];
    console.log(fateCard.picts_direction);
    setPickedFateCards([fateCard, ...pickedFateCards]);
    const afterRemovedInitialFateCards = initialFateCards.slice(1);
    setInitialFateCards(afterRemovedInitialFateCards);
  };

  const resetDrewFateCards = () => {
    setPickedFateCards([]);
  };

  return (
    <ThemeProvider theme={muiTheme}>
      <GoogleFontLoader
        fonts={[
          {
            font: 'Mitr',
            weights: [300, 400, 500],
          },
        ]}
      />
      <AppGradientWrapper>
        <AppContainer>
          <AppHeaderContainer>
            <AppPrimaryText>Hadrian's Wall Helper</AppPrimaryText>
          </AppHeaderContainer>
          <Grid container>
            <Grid item xs={4}>
              <ResourceCounterCard key={`card-resource`}>
                <AppPrimaryText>Resource</AppPrimaryText>
                <ResourceCounterSet />
              </ResourceCounterCard>
            </Grid>
            <Grid item xs={4}>
              <FateCardSection key={`form-card-fate-card`}>
                <AppPrimaryButton onClick={randomFateCard}>
                  Draw Fate Card
                </AppPrimaryButton>
                <AppPrimaryButton onClick={resetDrewFateCards}>
                  Reset Fate Card
                </AppPrimaryButton>
                {pickedFateCards &&
                  pickedFateCards.map((card) => (
                    <FateCardContainer
                      picts_direction={card.picts_direction}
                      gladiator={card.gladiator}
                      goods={card.goods}
                      resource={card.resource}
                    />
                  ))}
              </FateCardSection>
            </Grid>
          </Grid>
        </AppContainer>
      </AppGradientWrapper>
    </ThemeProvider>
  );
};

export default App;
