import React from 'react';
import ResourceCounter from '../resourceCounter/ResourceCounter';
import { CounterSetContainer } from './style';

interface Props {}

const ResourceCounterSet: React.FC<Props> = () => (
  <CounterSetContainer>
    <ResourceCounter textColor='black' colorText='Black' />
    <ResourceCounter textColor='blue' colorText='Blue' />
    <ResourceCounter textColor='purple' colorText='Purple' />
    <ResourceCounter textColor='yellow' colorText='Yellow' />
    <ResourceCounter textColor='grey' colorText='Brick' />
  </CounterSetContainer>
);

export default ResourceCounterSet;
